import Team from "./team";

class Config_Quick {
    teams: Team[];
    gameTime: number = 60;
    groupNumber: number = 2;

    constructor(teams: Team[]) {
        this.teams = teams;
    }

    addTeams () {
        if (this.teams.length < 4) {
            this.groupNumber++;
            this.teams.push({
                name: 'گروه ' + this.groupNumber,
                score: 0
            })
        }
    }

    removeTeams () {
        if (this.teams.length > 2) {
            this.groupNumber--;
            this.teams.splice(-1,1)    
        }
    }

    editTeams (id: number,newValue: string) {
        this.teams[id].name = newValue;
    }

    changeGameTimeValue (state: boolean) {
        switch (state) {
            case true:
                if (this.gameTime < 300) {
                    this.gameTime += 10;
                }
                break;
            case false:
                if (this.gameTime > 60) {
                    this.gameTime -= 10;
                }
            default:
                break;
        }
        
    }
}

export default Config_Quick;