import Team from "./team";

class Config_Normal {
  teams: Team[];
  laps: number = 3;
  thinkingTime: number = 5;

  constructor(teams: Team[]) {
    this.teams = teams;
  }

  addTeams() {
    if (this.teams.length < 4) {
      this.teams = [
          ...this.teams,
          new Team("گروه " + (this.teams.length + 1), 0)
      ];
    }
  }

  removeTeams() {
    if (this.teams.length > 2) {
      this.teams.splice(-1, 1);
    }
  }

  editTeams(id: number, newValue: string) {
    this.teams[id].name = newValue;
  }

  changeLapsValue(state: boolean) {
    switch (state) {
      case true:
        if (this.laps < 10) {
          this.laps++;
        }
        break;
      case false:
        if (this.laps > 3) {
          this.laps--;
        }
      default:
    }
  }

  changeThinkingTimeValue(increase: boolean) {
    switch (increase) {
      case true:
        if (this.thinkingTime < 60) {
          this.thinkingTime += 5;
        }
        break;
      case false:
        if (this.thinkingTime > 5) {
          this.thinkingTime -= 5;
        }
      default:
    }
  }
}

export default Config_Normal;
